const news = (state = [], action) => {
    switch (action.type) {
        case 'ADD_TODO':
            window.localStorage.setItem(action.id, JSON.stringify(action));
            return [
                ...state,
                {
                    id: action.id,
                    title: action.title,
                    content: action.content,
                    completed: false
                }
            ]
        default:
            return state
    }
}

export default news



